//rem
@function rem($px) {
    @return $px / 16px+0rem;
}

// Mixin for drawing button with optional argument for width, padding, and time
@mixin hover__b_effect($color, $color-hov, $width: 1px, $padding: 0.5em, $time: 0.2s) {
    position: relative;
    display: inline-block;
    color: $color;
    border-bottom: $width solid $color;
    cursor: pointer;
    white-space: nowrap;
    overflow: hidden;
    transition: color $time ease-in-out, background-color $time ease-in-out;
    &:after {
        content: '';
        position: absolute;
        right: 0;
        bottom: 0;
        height: 100%;
        width: $width;
        background: $color;
        transform: translateY(100%);
        transition: transform $time ease-in-out;
        transition-delay: $time * 3;
    }
    .btn__text {
        padding: $padding;
        position: relative;
        display: block;
        &:before,
        &:after {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            background: $color;
            transition: transform $time ease-in-out;
        }
        &:before {
            height: $width;
            width: 100%;
            transform: translateX(100%);
            transition-delay: $time * 2;
        }
        &:after {
            height: 100%;
            width: $width;
            transform: translateY(-100%);
            transition-delay: $time;
        }
    }
    &:hover:after,
    &:hover>.btn__text:before,
    &:hover>.btn__text:after {
        transform: translate(0, 0);
    }
    &:hover:after {
        transition-delay: 0s;
    }
    &:hover>.btn__text:before {
        transition-delay: $time;
    }
    &:hover>.btn__text:after {
        transition-delay: $time * 2;
    }
    &:hover {
        color: $color-hov;
        background-color: $color;
        transition-delay: $time * 3;
    }
}

@mixin hover__l_effect($color, $color-hov) {
    position: relative;
    color: $color;
    transition: all 0.7s ease-in-out;
    & {
        display: inline-block;
        overflow: hidden;
        &:before {
            content: '';
            top: 1px;
            left: 0;
            bottom: 0;
            right: 0;
            width: 100%;
            height: 100%;
            position: absolute;
            background: $color-hov;
            z-index: -1;
            transform: translateX(-100%) translateY(calc(100% - 2px));
            opacity: 0;
        }
        &:hover::before {
            transition: transform .7s cubic-bezier(.19, 1, .22, 1);
            transform: translateX(0%) translateY(calc(100% - 2px));
            opacity: 1;
            z-index: 1;
        }
        &:after {
            content: '';
            top: 1px;
            left: 0;
            bottom: 0;
            right: 0;
            width: 100%;
            height: 100%;
            position: absolute;
            background: $color-hov;
            z-index: 1;
            transition: transform .8s cubic-bezier(.19, 1, .22, 1);
            transform: translateX(100%) translateY(calc(100% - 2px));
            opacity: 1;
        }
        &:hover::after {
            transition: none;
            transform: translateX(0%) translateY(calc(100% - 2px));
            opacity: 0;
        }
    }
}

@mixin hover__rec_effect {
    svg rect {
        stroke-dasharray: 400, 0;
        transition: all 0.8s ease-in-out;
    }
    &:hover svg rect {
        stroke-width: 3;
        stroke-dasharray: 65 188;
        stroke-dashoffset: 65;
        transition: all 0.8s ease-in-out;
    }
}

@mixin hover__slip_up__effect {
    .services__description:hover {
        top: 0;
        justify-content: space-between;
        transform: translate3d(0,-50%,0);
    }
    .heading__sb:hover {
        transform: translate3d(0, -50%, 0) translate3d(0, -40px, 0);
    }
}