* {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: normal;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

ul {
  padding: 0;
  margin: 0;
  li {
    list-style: none;
  }
}

p {
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}


footer {
  position: relative;
  width: 100%;
}

html,
body {
  width:100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  color: $black;
  font-family: $proxima_regular;
  font-size: rem(16px);
  line-height: 1.42;
  @include portrait{
    line-height: 1.20;
  }
  @include landscape__phones{
    font-size: rem(13px);
  }
  // -webkit-font-smoothing: antialiased;
  // -moz-osx-font-smoothing: grayscale;
}